@import "../css/normalize.css";
@import "../css/skeleton.css";
@import "../css/chessboard.css";
button,
input[type="submit"] {
  background: linear-gradient(to bottom, #ffffff 0%, #f7f7f7 100%);
  color: #000;
  font-size: 1.1em;
}
button.small,
input[type="submit"].small {
  font-size: .8em;
}
input[type="submit"] {
  display: block;
}
input,
select,
textarea {
  color: #000;
  width: 100%;
}
label {
  display: block;
  margin-top: 1em;
}
label + input,
label + select {
  margin-top: 0;
}
input + div > div.error,
select + div > div.error {
  color: #F00;
  margin-top: -1em;
  margin-bottom: 1em;
}
input[type="hidden"] + div > div.error {
  margin-top: 0;
}
#messages {
  position: fixed;
  width: 66%;
  margin-left: 20px;
  margin-top: -40px;
  z-index: 100;
}
div.message {
  box-sizing: border-box;
  width: 100%;
  margin-bottom: 15px;
  padding: 10px 10px 10px 20px;
  box-shadow: 0px 3px 12px -7px rgba(0, 0, 0, 0.74);
  background: linear-gradient(135deg, #9d9d9d 0%, #a1a1a1 3%, #808080 3%, #7c7c7c 100%);
}
div.message.error {
  background: linear-gradient(135deg, #f85032 0%, #f16f5c 3%, #f6290c 3%, #f02f17 71%, #e73827 100%);
}
div.message.success {
  background: linear-gradient(135deg, #9dd53a 0%, #a1d54f 3%, #80c217 3%, #7cbc0a 100%);
}
div.message.loading {
  background: linear-gradient(135deg, #dcdc34 0%, #dadd47 3%, #c4c713 3%, #bebe08 100%);
}
.animate-repeat {
  line-height: 30px;
  list-style: none;
  box-sizing: border-box;
}
.animate-repeat.ng-move,
.animate-repeat.ng-enter,
.animate-repeat.ng-leave {
  transition: all linear 0.5s;
}
.animate-repeat.ng-leave.ng-leave-active,
.animate-repeat.ng-move,
.animate-repeat.ng-enter {
  opacity: 0;
  max-height: 0;
}
.animate-repeat.ng-leave,
.animate-repeat.ng-move.ng-move-active,
.animate-repeat.ng-enter.ng-enter-active {
  opacity: 1;
  max-height: 30px;
}
table {
  width: 100%;
}
tbody tr {
  background-color: rgba(0, 0, 0, 0.4);
}
tbody tr:nth-child(even) {
  background-color: rgba(0, 0, 0, 0.2);
}
tbody tr.selected {
  background-color: rgba(3, 90, 3, 0.8);
}
header nav ul li {
  display: inline-block;
  border-bottom-right-radius: 5px;
  border-bottom-left-radius: 5px;
  background: linear-gradient(to bottom, #ffffff 0%, #f7f7f7 100%);
  padding: 2px 10px;
  margin-left: .7em;
}
header nav ul li a {
  color: #000;
  text-decoration: none;
}
div.game-log {
  width: 100%;
  background: rgba(100, 100, 100, 0.8);
  padding: 10px;
}
#overlay {
  display: none;
  background-color: rgba(0, 0, 0, 0.1);
  position: fixed;
  z-index: 100;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
}
div.layer {
  display: none;
  position: fixed;
  z-index: 101;
  margin-left: -30%;
  left: 50%;
  top: 15%;
  width: 60%;
  min-width: 400px;
  min-height: 300px;
  padding: 30px;
  background-color: #FFF;
  box-shadow: 0 0 20px 1px #888;
}
div.layer.small {
  margin-left: -15%;
  width: 30%;
}
#accounts-list {
  list-style: none;
}
#accounts-list li {
  padding-left: 40px;
  background-repeat: no-repeat;
  background-position: left center;
  color: #666;
  cursor: pointer;
}
#accounts-list li span {
  color: #000;
  font-weight: bold;
  display: block;
  margin-bottom: -3px;
}
#accounts-list li.selected {
  background-color: #D4FFC9;
}
div.selectedAccount {
  padding-left: 40px;
  background-repeat: no-repeat;
  background-position: left center;
  font-weight: bold;
}
body {
  font-family: 'Open Sans', sans-serif;
  background: #000;
}
body.metapage {
  background: #000 url(../img/chess.jpg) no-repeat fixed 0 0 / cover;
}
.metapage .container {
  max-width: 600px;
}
div.container.space-top {
  margin-top: 4em;
}
div.bg-overlay {
  background-color: rgba(0, 0, 0, 0.77);
  width: 100%;
  height: 100%;
  position: fixed;
  left: 0;
  top: 0;
  z-index: -1;
}
main {
  color: #FFF;
}



